.ico{
	display: inline-block;
	vertical-align: middle;
	font-size: 0;
	line-height: 0;
	background-size: cover !important;
	background-position: 0 0 !important;
	background-repeat: no-repeat !important;
}
.ico-ducat{
	--w:calc(45px / 2);
	--h:calc(41px / 2);
	width: var(--w);
	max-width: var(--w);
	flex:0 0 var(--w);
	height: var(--h);
	background-image: url("data:image/png;base64,");
}
@mixin arrow {
	--w:calc( 21px / 2);
	--h:calc( 34px / 2);
	width: var(--w);
	width: var(--w);
	max-width: var(--w);
	flex:0 0 var(--w);
	height: var(--h);
	background: url(/images/map/icons/arrow.png) 0 0 no-repeat;
	background-size: cover;
}
.arrow{
	@include arrow;
}