.rectangle{
	--size_corner_local:calc(var(--size_corner) / 2.7);
	position: relative;
	width: 67px;
	height: 102px;
	display: block;
	padding: 0;
	border: 0;
	background: none;
	--color_shadow:rgba(0,0,0,0.7);
	&.type_danger{
		--color_shadow:rgba(255,0,0,0.6);
	}
	&.type_active{
		--color_shadow:rgba(255,185,0,0.6);
		.rectangle_mask{
			&:after{
				// rgba(255,185,0,0.3)
				box-shadow:inset 0 0 15px var(--color_shadow) , inset 0 0 0 1px rgba(255,185,0,0.3);

			}
		}
	}
	&.type_disabled{
		opacity: .3;
		cursor: inherit;
		.rectangle_mask{
			&:before{
				content: none;
			}
		}
		&:hover{
			img{
				transition:none;
				filter:none;
			}
		}
	}
	&.type_warning{
		--color_shadow:rgba(255,111,0,0.6);
	}
	&.type_primary{
		--color_shadow:rgba(0,144,255,0.6);
	}
	@include media(max, lg) {
		height: 85px;
	}
	.rectangle_mask{
		width: 100%;
		height: 100%;
		position: relative;
		&:before,&:after{
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			height: 100%;
			background: #a5aca7;
			mix-blend-mode: saturation;
			z-index: 1;
		}
		&:after{
			mix-blend-mode:normal;
			background: none;
			box-shadow:inset 0 0 15px var(--color_shadow);
		}
	}
	&:before,&:after{
		content: "";
		position: absolute;
		top: -1px;
		left: -1px;
		width: var(--size_corner_local);
		height: var(--size_corner_local);
		background: var(--corner_img) no-repeat;
		background-size: cover;
		pointer-events: none;
		z-index: 4;
	}
	&:after{
		left: auto;
		top: auto;
		bottom: -2px;
		right: -1px;
		transform:scale(-1,-1);
	}
	img{
		z-index: 3;
		@include centered_img(100,100);
	}
}
